import React, { useState, forwardRef } from 'react'
import styled from 'styled-components'
import _ from 'lodash'
import TextInput from '../TextInput/index'
import Dropdown from '../Dropdown/index'
import { googleAPIRenderPlaces } from './googleApiService'
import color from '../color-css/color'
import ValidationIndicator from '../ValidationIndicator'

export type Props = {
  onSelectedItemProp?: (wasSelected: boolean) => void
}

const Label = styled.label`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  color: ${color.blue_17_percent};
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`

let idTimeOut: number;
type Ref = HTMLInputElement

const AddressAutosuggest = forwardRef<Ref, Props>((props: any, aRef) => {
  const [inputSuccess, setInputSuccess] = useState(false);
  const [visibility, setVisibility] = useState(false);
  const [dropdownSuggestionContent, updateSuggestionContent] = useState([''])
  const [textInputContent, updateTextInputContent] = useState('')
  const [textTyping, setTextTyping] = useState('')
  const [isShow, setIsShow] = useState(false);

  const handleOnInputSucess = (inputSuccess: boolean) => {
    setInputSuccess(inputSuccess)
  }

  const handleOnVisibility = (visibility: boolean) => {
    setVisibility(visibility);
  }

  const handleContent = (content: string) => {
    let streetaddress = content.substr(0, content.indexOf(','));
    updateTextInputContent(streetaddress)
    props.onSelectedItemProp ? props.onSelectedItemProp(inputSuccess) : {}
  }

  const handleOnShow = (isShow: boolean) => {
    setIsShow(isShow)
  }


  const renderSuggestion = (inputContent: string) => {
    clearTimeout(idTimeOut);
    idTimeOut = setTimeout(async () => {
      try {
        if(inputContent.length < 2) {
          return
        }
        const response = await googleAPIRenderPlaces(inputContent)
        const result = response.data.predictions
        let array: string[] = []
          
        result.forEach((each: any) => {
          let stringToGoIntoTheRegex = inputContent
          let regex = new RegExp("" + stringToGoIntoTheRegex + "", "gi")
          let suggestString = each.description.match(regex)
          const part1 = each.description.substring(0, suggestString[0].length)
          const part2 = each.description.substring(suggestString[0].length, each.description.length)
          const stringAddress = part1+ ',' + part2
          array.push(stringAddress)
        })
        updateSuggestionContent(array)

      } catch (error) {
      }
    }, 2000)
  }

  let msg = '';
  if (inputSuccess === true ) {
    msg = 'Thanks'
  } else {
    msg = 'Error'
  }
  
  return (
    <div>
      <Label>Home address <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>*</LabelIcon></Label>
      <TextInput
        placeholder="Type your address here"
        widthSize="34rem"
        paddingSize="0.8125rem 5.375rem 0.75rem 1.5625rem"
        name="address"
        type="text"
        onInputSucess={sucess => handleOnInputSucess(sucess)}
        onVisibility={visible => handleOnVisibility(visible)}
        onShow={isShow => handleOnShow(isShow)}
        onTypeText={textInputValue => {
          if (textInputValue !== textTyping) {
            setTextTyping(textInputValue)
            renderSuggestion(textInputValue)
          }
        }}
        inputFromProps={textInputContent}
        ref={aRef}
      />

      <Dropdown
        value={content => handleContent(content)}
        isVisible={isShow}
        renderingSuggestions={dropdownSuggestionContent}
        footerContent="Cant find your address? Continue typing"
      />

      {msg && <ValidationIndicator isCorrect={inputSuccess} {...{message:msg, visibility}} />}
    </div>
  )
})

export default AddressAutosuggest