import React from 'react'
import styled from 'styled-components'
import ButtonItem from './ItemButton'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons'

const StyledIconChecked = styled(FontAwesomeIcon)`
  color: #189126;
  width: 22px;
  height: 22px;
  text-align: center;
`
const Container = styled.div`
  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
`
const ContainerInfo = styled.div`
  display: flex,
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  margin-top: 11px
`

interface RadioButtonContaiter {
  className?: string
  visibility?: boolean
}

const RadioButtonContaiter = styled.div<RadioButtonContaiter>`
  display: ${props => props.visibility === true ? 'block' : 'none'};
  @media screen and (max-width: 768px) {
    // padding: 15px;
  };
`

export const RadioButtonGroup = (props:any) => {
  const { className, label, title1, title2, title3, visibility }  = props
  const [activeId, changeActiveId] = React.useState(3)

  return (
    <RadioButtonContaiter 
      className={className}
      visibility={visibility}
    >
      <Label>{label}
        {' '}
        <LabelIcon onInputSucessAsLabelIconColor={true}>*</LabelIcon>
      </Label>
      <Container>
        <ButtonItem
          active={activeId == 0}
          title={title1}
          onPress={() => { changeActiveId(0) }} />
        <ButtonItem
          active={activeId == 1}
          title={title2}
          onPress={() => { changeActiveId(1) }} />
        <ButtonItem
          active={activeId == 2}
          title={title3}
          onPress={() => { changeActiveId(2) }} />
      </Container>
      <ContainerInfo>
        <StyledIconChecked icon={faCheckCircle} style={{ display: activeId == 3 ? 'none' : 'block' }} />
      </ContainerInfo>
    </RadioButtonContaiter>
  )
}
export default RadioButtonGroup
